.example {
    width: 18rem;
}

.example-header {
    padding: 3rem 5rem 0;
    max-width: 1200px;
    margin: 0 auto;
    text-align: left;
}

.example-row {
    /* border: 2px solid red; */
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-align-items: center;
            align-items: center;
    -webkit-flex-flow: nowrap;
            flex-flow: nowrap;
    max-width: 1200px;
    margin: 0 auto;
    padding: 3rem 5rem 0;
}

.img_box {
}

.img_box > img {
    max-width: 286px;
    max-height: 170px;
}

.example-row ~ .example-row {
    padding-bottom: 3rem;
}

.fa-github:before, .fa-markdown:before {
    margin-right: 0.4rem;
}

