.card-body {
    text-align: left;
}

@media screen and (max-width: 1183px) {
    .example-header {
        padding: 2rem 4rem 0;
    }
    .example-row {
        padding: 2rem 4rem 0;
    } 
}

@media screen and (max-width: 1083px) {
    .example-header {
        padding: 2rem 3rem 0;
    }
    .example-row {
        padding: 2rem 3rem 0;
    } 
}

@media screen and (max-width: 991px) {
    .example-header {
        padding: 2rem 2rem 0;
    }
    .example-row {
        padding: 2rem 2rem 0;
    } 
}
@media screen and (max-width: 929px) {
    .example-header {
        padding: 2rem 1rem 0;
    }
    .example-row {
        padding: 2rem 1rem 0;
    } 
}
@media screen and (max-width: 860px) {
    .example-header {
        padding: 2rem 1rem 0;
    }
    .example-row {
        padding: 2rem 0 0;
    } 
}
@media screen and (max-width: 600px) {
    .example {
        min-width: 90%;
        margin-top: 1rem;
    }
    .img_box > img {
        max-width: 100%;
        max-height: 100%;
    }
    .example-header {
        padding: 1.5rem;
    }
    .example-title > h1 {
        font-size: 2rem;
    }
    .example-title > h3 {
        font-size: 1.5rem;
    }
    .example-row ~ .example-row {
        padding-bottom: 1rem;
    }
    .example-row {
        -webkit-justify-content: center;
                justify-content: center;
        -webkit-flex-flow: wrap;
                flex-flow: wrap;
        text-align: center;
        padding: 0;
    } 
}

.footer {
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    padding: 1rem;
}
